import { model, Model, htmlDecode } from '@engine/core';
import { XWaterMark } from './watermark';

@model()
export class ReportWateramrkModel extends Model {
    public static xtype = 'report.model.watermark';

    private options: {
        watermark: Partial<XWaterMark>;
    };

    public state() {
        return {
            watermark: this.options.watermark || {},
        };
    }

    public computed = {
        transformedWatermark: () => this.model.watermark ? {
            ...this.model.watermark,
            text: htmlDecode(this.model.watermark.text),
            fontWeight: 'normal',
        } : {},
    };

    public actions = {
        setWatermark: (watermark: Partial<XWaterMark>) => {
            this.model.watermark = watermark;
        },
    };
}
